import React from 'react';
import { MediaPlayer } from '@cassette/player';
import '@cassette/player/dist/css/cassette-player.css';

import { getRelease } from '../data.js'

export default function Playlist(props) {
    const playlist = [];
    if (props.track) {
      playlist.push({url: props.track, title: props.title});
    } else {
      const releaseId = props.releaseId ? props.releaseId : "NE003";
      const release = getRelease(releaseId);
      if (release.audio && release.audio.length > 0) {
        if (typeof release.audio[0] === "string") {
          release.audio && release.audio.forEach((audioFile, i) => playlist.push({url: audioFile, title: `${release.name} - ${i + 1}`}));
        } else if (typeof release.audio[0] === "object") {
          release.audio && release.audio.forEach((audioFile, i) => playlist.push({url: audioFile.file, title: `${release.name} - ${audioFile.title}`}));
        }
      }
    }
    
    return (
      <div className={`${props.className} ne-audio-player-wrapper`}>
          <MediaPlayer playlist={playlist} controls={['backskip', 'playpause', 'forwardskip', 'progress']}/>
      </div>
    );
}