        "id": "NEx11",
        "name": "Say his Name",
        "artist": "Leo from Vermont",
        "date": "June 2020",
        "description": "Dear world:\n\nAs a white person of privilege I've been struggling with what to do about this (and so many other) tragedies, and how to help in the inspiring response by people taking to the streets to demand justice. As was put so eloquently by Josiah Steinbrick, we can't make this about people like me, we need to give space to other voices. But I felt like I needed to do something. So I went through the mountains of African American music I've been fortunate enough to have shared with me from a staggering variety of sources and made a mix tape to try to help.\n\nI've done my best to be sensitive to the cultural appropriation inherent in any such endeavor, but please know I did my best to present these voices and music in a way that made sense. Side A is mostly music with vocals/poetry while side B is more often instrumental, with exceptions.\n\nI was watching on twitter etc as everything was happening and I was working, so I used audio live streamed from various protests on the mix. Thank you to all the brave people out there doing good.\n\nLet's all take care of each other.",
