        "description": "It was born deep in a basement house party, circa 1996. As far as unlikely outposts go, Vermont might as well be outer space. But the mighty John Demus hit the turntables, joyride riddim thumping. We were surrounded by bashment girl, the cheap bottle of E&J almost gone, and it was on. Quick slide through ganja pipe with the buccaneer, then it all crashed. First bars of lodge blew the minds clean out of the skulls of the youth, and the path was clear. Shit, Babylon thumping on the door. I didnâ€™t even know what that meant. But FUCK that, lock it up, throw on grass cyaat and settle. Canâ€™t let them kill the vibe.\n\nNext night, red solo cups, three blocks down. Then the next. Cool rulers ruled, but it was soon forward. Time moved along, but the precedent was established. An elder dread once told me you never break up a riddim, you RUN a riddim. So we ran with it. Mixtapes, mix CDs (remember when that seemed like the future?), so many sets, and the top was the late night sessions. 151 always seemed like a good idea, and Dubie could make it work. It always felt right. But then Infinite went in. The only constant was change.\n\nBut look at me, trying to wax poetic about sweaty basement parties in dirty apartments with the locals. Iâ€™m not glorifying it, because that would be fake, but it was fun as hell.\n\n\nSKRW II was a second life for the first Screwface tape. A taste of dubbed out island life from the depths of Duxbury. Sent out to the Waxidermy massive to little fanfare, it was a shot at recreating the feeling of those long walks home from aforementioned parties, often through the woods with Adam to avoid Burlingtonâ€™s finest. Only the meek get pinched, the bold survive. Those nights changed the way I felt about music, of all forms and fashions. We run tings, tings nah run we.\n\nThis new tape is less a nod to the past but more a look at the future, with a hand held out to friends new and old. Ged Gengras made the master sing in a way we never could alone, Iâ€™m forever in his debt. I Jahbar and G Sudden lend endless hype to the beginning and end of the tape, a blessing from the Duppy Gun crew that inspires us every time we create. Itâ€™s an honor to be in the family.\n\nAll profit from this release will go directly to the Duppy Gun studio in Kingston, Jamaica. Letâ€™s build. We hope this is merely the first collaborative release and we continue to vibe from here. Ever forward famâ€¦\nJah Meditation Sound &\nLeo from Vermont &\nThe NightErrands crew ",
        "image": "https://f4.bcbits.com/img/a3369620088_16.jpg",
        "image_preview": "https://f4.bcbits.com/img/a3369620088_16.jpg",
