`
{{image https://nighterrands.s3.amazonaws.com/thoughts/th04/th04.png "Vibes"}}
Robert Elliot Kahn. Tim Berners-Lee. William von Meister. Our beautiful, digital time capsule.

There's beauty in many things. Divinity too, if your philosophy is just right. The obvious: poetry, a third night moon, those first two weeks of spring, camaraderie. The obscure: closing time at the Moretown Store, watching the trains go by, green against gray, a shared understanding. Maybe I'm just ruminating about scale, concomitance, parallels? The internet loves scale.

Choice links, to me, is the art of finding that obscure beauty in this dumpster dive we call surfin' the web. I think there's also value in not really understanding what's going on at all. That militaristic shock and awe approach. I want to be left a bit dumbfounded by that link. But that's ego talking - you all know a choice link when you see one. And these are Mike's links, not mine, so go ahead and ignore everything I just said.

{{image https://nighterrands.s3.amazonaws.com/thoughts/th04/wow.png "Gettin' after it"}}
A wash of red. Low framerate. Youthful exuberence.
{{image https://nighterrands.s3.amazonaws.com/thoughts/th04/yup.png "Composition"}}
Low ceilings. A mediocre cameraperson. Humility.

This is a powerful link. It's hypnotic. There are so many wonderful factors combining to elevate this to the status of "choice". The contrast of the at times reckless abandon and the tender interstitial movements really gets me. Selector put on a true banger for this one (not to mention the audio still somehow being copyrighted by Sony Music). The whole thing being bathed in this wash of red light is truly the crowning piece. As of time of publish I would wager I'm responsible for at least 10% of the 364 views. After a frontloaded burst, this link has become a quarterly returner for me. The time off lets it simmer, giving me fresh insight for every revisit. And wow, do I get a lot from every viewing. The video description informs us that we're witnessing "Nikhil Karkhanis Solo dance competition performance". Nikhil, thank you for sharing your competition performance, it has brought me a lot of joy. Please enjoy this choice link.
{{embed "<iframe width="560" height="315" src="https://www.youtube.com/embed/RhNGTmzDYCI" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>"}}
`
