`
{{embed "<iframe width="560" height="315" src="https://www.youtube.com/embed/t0D6z-3KTCk" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>"}}
Hey everyone, Mike here. Sometimes my Choice Link will be some choice HEMA commentary from the head trainer at Duxbury HEMA alliance. We're heading into dark times, people, and we should all be more proficient with a variety of blades for when duels are used to settle disputes.

'Foord, hey! Hey! A little bit with the overextension there, maybe try to keep more on center after a slice. Always finish in LongPoint or Reilly is going to eat your lunch.  Hey boys, make sure to have something planned beyond initially taking the Vor. You don't want Reilly to ruin your shirt there by poking a hole through it. Pretend he's a ridge runner who's trying to steal your pig. 

â€œYou all right?â€
â€œYeah!â€

Way to check in, good with the safety. Riley definitely gets the Green Mountain HEMA Dojo Good Sport award this week. What an honor. Sportsmanship is next to godliness.

Hey good buddies, your pluderhose are dank but I could probably grab a handful of them if we're playing dirty Green Mountain rules, so maybe hike those things up with a good cod piece. Make sure it's good'n tight so your pluderhose stay in place and I can't grab them and yank them off and then you lose the duel due to your bare butt hanging out.

Okay, despite all this praise, there's very little kicking and that's really disheartening. There's a lack of shoes, so I want to see more kicking. No reason not to kick. I mean, a shin kick is very painful, just make sure not to break your own damn foot. I guess if you've never kicked don't start now.

You guys are looking like you're sucking wind pretty bad already, maybe take a few laps after we're done, eh? It telegraphs your movement when you lead with your feet, fellas. I'm going to say it three times for every time you do itâ€” what if a redneck is trying to take your farm?!

I'm going to need to see more zwerch. It's Meyer rapier, so you need to pretend you're a helicopter on the nachslag or Reilly is going to cut off the crusts, feed them to your dog, and eat your damn bologna sandwich that your mom made for you.

Fun fact, we sprang Reilly out of Medieval Times to come down to Night Errands studio and chat a little bit about the making of the video, but he had to cancel because he says his pluderhose got caught in his bike chain. That's why I always say you should drive a car to a duel.

Just a note about equipment at the dojo here, guysâ€“You know, 'Foord, I'm not entirely sure you should be doing this bladework without gloves, and Reilly, I love you to death, but you've clearly had the whole bottle of blackberrry brandy and you're hacking like a real jerk.

Let's work on those kicks, and let's work on our problem solving; we shouldn't be doing violence to one another. Once we've all had a water break, let's get into LongPoint with your non-dominant hand, and tune into this recording:
{{embed "<iframe src="https://archive.org/embed/podcast_meditation-mindfulness-b_guided-relaxation-response-med_1000411319005" width="500" height="30" frameborder="0" webkitallowfullscreen="true" mozallowfullscreen="true" allowfullscreen></iframe>"}}`
