import React from 'react';
import Carousel, { Modal, ModalGateway } from 'react-images';
 
class ZoomableImage extends React.Component {
    constructor() {
        super();
        this.state = {
            modalIsOpen: false
        };

        this.toggleModal = this.toggleModal.bind(this);
    }

    toggleModal() {
        this.setState(state => ({ modalIsOpen: !state.modalIsOpen }));
    }
    
    render() {
        let { smallImageSrc, largeImageSrc, smallImageClass, divStyle, height, width, images, index } = this.props;
        let views = images;
        if (!largeImageSrc) {
            largeImageSrc = smallImageSrc;
        }
        if (!images) {
            views = [{ source: largeImageSrc, className: 'img-fluid' }];
        }
        const carouselIndex = index ? index : 0;
        const style = {
            maxHeight: height,
            maxWidth: width
        };
        const smallImageClasses = smallImageClass + ' ne-zoomable-image';
        return (
            <div style={divStyle}>
                <img 
                    onClick={this.toggleModal} 
                    className={smallImageClasses} 
                    src={smallImageSrc} 
                    alt="Night Errands" 
                    style={style}
                />
                {/* <ModalGateway>
                    {this.state.modalIsOpen ? (
                        <Modal onClose={this.toggleModal}>
                            <Carousel views={views} currentIndex={carouselIndex} />
                        </Modal>
                    ) : null}
                </ModalGateway> */}
            </div>
        )
    }
  }

export default ZoomableImage;